@import url(https://fonts.googleapis.com/css2?family=Days+One&family=Nobile:wght@400;500;700&family=RocknRoll+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Be+Vietnam+Pro:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yantramanav:wght@500&display=swap);
html {
  width: 100%;
  height: 100%;
}

body {
  min-height: 100%;
  background-color: #040704;
  position: relative;
  color: #fff;
  z-index: 0;
  display: table;
  width: 100%;
  height: 100%;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  display: table;
  width: 100%;
  min-height: 100%;
  margin: auto;
  text-align: center;
  vertical-align: middle;
}

.App {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  min-height: 100%;
  width: 100%;
  height: 100%;
  margin: auto;
}

@media (min-height: 375px) {
  .App {
    min-height: 375px;
  }
}

#cover {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

#shader {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  background-color: rgba(25, 25, 25, 0.5);
}

.bg-img {
  width: 100%;
  height: 100%;
  object-position: 50% 50%;
  object-fit: cover;
}

.w200px {
  width: 200px;
}

.Main {
  margin: 0;
}

@media (max-height: 319px) {
  .Main {
    margin-bottom: 3rem;
  }
}

.Heading {
  margin-bottom: 5vh;
}

.Heading h1 {
  line-height: 1em;
  font-weight: 500;
  font-size: 5em;
  font-family: 'RocknRoll One', sans-serif;
  text-transform: capitalize;
  margin: 0;
}

@media (min-width: 1000px) {
  .Heading h1 {
    font-size: 9em;
  }
}

.Intro {
  margin-top: 0;
}

.Intro p {
  padding: 0 30px;
  font-family: 'Be Vietnam Pro', 'open-sans', sans-serif;
  line-height: 1.4em;
  letter-spacing: 0.2em;
  font-weight: 400;
}

@media (min-width: 700px) {
  .Intro p {
    padding: 0 90px;
  }
}

@media (min-width: 800px) {
  .Intro p {
    padding: 0 160px;
  }
}

@media (min-width: 1000px) {
  .Intro p {
    padding: 0;
  }
}

@media (min-width: 760px) {
  @media (min-height: 760px) {
    .Intro {
      margin: 70px auto;
    }

    .Intro p {
      padding-bottom: 10px;
    }
  }
}

.Navbar {
  margin-top: 4vh;
}

.Navbar a {
  text-decoration: underline;
  color: white;
}

.list-group-item {
  background-color: transparent;
  margin: 0;
  padding: 0;
  border: 0;
}

.Navbar .list-group-item {
  background-color: transparent;
  margin: 0;
  color: white;
}

.Navbar .list-group-item.hover {
  color: #ffc;
}

.front-nav-bar ul {
  font-family: 'Yantramanav', helvetica, sans-serif;
  text-transform: uppercase;
  font-size: 1em;
  letter-spacing: 0;
  padding: 0;
}

.front-nav-bar li {
  -webkit-appearance: none !important;
}

@media (min-height: 320px) {
  .Navbar {
    margin-bottom: 12vh;
  }

  .list-group-item {
    font-size: 1.3em;
  }
}

@media (min-height: 375px) {
  .list-group-item {
    font-size: 1.8em;
  }
}

@media (max-width: 475px) {
  .list-group {
    display: block;
  }
}

@media (max-width: 1000px) {
  @media (min-height: 1000px) {
    .Navbar .list-group {
      display: block;
    }

    .Navbar .list-group-item {
      margin-top: 20px;
    }
  }
}

.Footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
}

.Footer img {
  width: 36px;
}

.Footer img.hover {
  width: 44px;
}

.Footer img.unhover {
  opacity: 0.8;
}

.Footer div {
  -webkit-appearance: none !important;
}

@media (max-height: 319px) {
  .social-media-bar {
    margin-bottom: 0.5rem !important;
  }
}

@media (min-height: 320px) {
  .social-media-bar {
    margin-bottom: 0.8rem !important;
  }
}

@media (min-height: 370px) {
  .social-media-bar {
    margin-bottom: 1.2rem !important;
  }
}

@media (min-height: 400px) {
  .social-media-bar {
    margin-bottom: 1.5rem !important;
  }
}

@media (min-height: 600px) {
  .social-media-bar {
    margin-bottom: 2.5rem !important;
  }
}

@media (min-height: 800px) {
  .social-media-bar {
    margin-bottom: 3.5rem !important;
  }
}

@media (min-height: 1000px) {
  .social-media-bar {
    margin-bottom: 4.5rem !important;
  }
}

.Modal {
  color: black;
}

.modal-body h3 {
  text-align: center;
}

.modal-body img {
  width: 100%;
  max-width: 400px;
  padding: 10px 30px;
}

@media (min-width: 500px) {
  .modal-body img {
    width: 100%;
    max-width: 200px;
    padding: 10px 30px;
  }
}

button {
  text-transform: capitalize;
  -webkit-appearance: none !important;
}

